.logo {
    max-width: 5%;
}

.alert {
    --background: #f5f1e7;
}

.frequency-waveform-channels {
    display: flex;
    padding-top: 20px;
    height: 60%;
}

.channel-div {
    width: 22%;
    display: flex;
    justify-content: center;
}

.edit-button-contents {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.edit-img {
    width: 80%;
    margin-top: 1%;
}

.edit-button-L {
    width: 75%;
    height: 60%;
    margin-top: 2%;
    margin-left: 20%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.edit-button-R {
    width: 75%;
    height: 60%;
    margin-top: 2%;
    margin-right: 20%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.frequency-and-waveform {
    width: 56%;
}

.frequency-button-div {
    display: flex;
    justify-content: center;
    height: 25%;
    margin-bottom: 2%;
}

.frequency-button {
    font-size: xx-large;
    height: 100%;
    width: 90%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.range-frequency {
    --bar-background-active : #f5f1e7;
    --bar-background : #808080;
    --bar-height : 25px;
    --knob-size :30px;
    --knob-border-radius : 4px;
    --knob-background : #d00000;
    --knob-box-shadow: 0px 0px 0px 4px #f5f1e7;
    padding: 0px 20px;
    padding-bottom: 10px;
}

.waveform-buttons {
    display: flex;
    justify-content: center;
    height: 50%;
}

.waveform-button {
    width: 19%;
    height: 75%;
    margin: 2px 5px;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.waveform-icon-1 {
    position: absolute;
    height: 80%;
    width: 140%;
}

.waveform-icon-2 {
    position: absolute;
    width: 125%;
}

.amplitude-start-phase {
    display: flex;
    height: 35%;
    padding-top: 10px;
    padding-bottom: 10px;
}

.amplitude-components {
    display: flex;
    flex-direction: column;
    padding-left: 5%;
    width: 35%;
}

.amplitude-button {
    font-size: x-large;
    margin-left: 5%;
    margin-right: 5%;
    max-height: 40%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.range-amplitude {
    --bar-background-active : #f5f1e7;
    --bar-background : #808080;
    --bar-height : 25px;
    --knob-size :30px;
    --knob-border-radius : 4px;
    --knob-background : #d00000;
    --knob-box-shadow: 0px 0px 0px 4px #f5f1e7;
    padding: 0px 14px;
}

.start-buttons {
    display: flex;
    justify-content: center;
    margin-top: 10px;
    width: 30%;
}

.hold-button {
    font-size: large;
    height: 70%;
    margin: 0 5px;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.play-pause-img {
    width: 50%;
    margin-top: 4px;
}

.phase-components {
    display: flex;
    flex-direction: column;
    padding-right: 5%;
    width: 35%;
}

.phase-button {
    font-size: x-large;
    margin-left: 5%;
    margin-right: 5%;
    max-height: 40%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.range-phase {
    --bar-background-active : #f5f1e7;
    --bar-background : #808080;
    --bar-height : 25px;
    --knob-size :30px;
    --knob-border-radius : 4px;
    --knob-background : #d00000;
    --knob-box-shadow: 0px 0px 0px 4px #f5f1e7;
    padding: 0px 14px;
}

ion-popover.input-popover {
    --width: 45%;
    --background: #f5f1e7;
    --backdrop-opacity: var(--ion-backdrop-opacity, 0.7);
    top: -12%;
    left: -2.5%;
}

ion-popover.input-popover-web {
    --width: 45%;
    --background: #f5f1e7;
    --backdrop-opacity: var(--ion-backdrop-opacity, 0.7);
    left: -25%;
    top: -47.5%;
}

.popover-content {
    display: flex;
    margin: 10px 20px;
}

.input-popover-text {
    font-size: x-large;
    color: #000000;
}

